.container {
   display: flex;
   justify-content: space-between;
   background-color: rgba(81, 210, 249, 0.63);
   box-shadow: 3px 3px 5px black;
   border-radius: 20px;
   padding: 20px;
   letter-spacing: 1px;
   cursor: pointer;
}

.active {
   color: aqua;
   background-color: black;
}
