.container {
   background-color: bisque;
   padding: 20px;
   margin: 20px auto;
   border-radius: 20px;
   width: 800px;
   overflow: auto;
   scroll-behavior: smooth;
   max-height: 540px;

   & div {
      margin-bottom: 20px;
   }

   & :last-child {
      margin-bottom: 0;
   }
}
